    queryProjects() {
      this.map.clearOverlays()
      this.loadProjects().then(() => {
        this.drawIcons()
      })
    },
    initMap(container) {
      const map = new BMap.Map(container)
      const province = getItem(constant.province)
      const city = getItem(constant.city)
      if (city) {
        map.centerAndZoom(city, 10)
      } else if (province) {
        map.centerAndZoom(province, 10)
      } else {
        map.centerAndZoom('北京市', 10)
      }
      map.enableScrollWheelZoom(true) // 启用滚轮放大缩小
      setTimeout(() => {
        if (map) {
          const top_left_navigation = new BMap.NavigationControl() // 左上角，添加默认缩放平移控件
          map.addControl(top_left_navigation)
          map.addControl(new BMap.MapTypeControl(
            {
              mapTypes: [
                BMAP_NORMAL_MAP,
                BMAP_HYBRID_MAP
              ] }
          ))
          const ctrlOve = new window.BMap.OverviewMapControl({
            anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
            isOpen: 1
          })
          map.addControl(ctrlOve)
        }
      }, 1000)
      return map
    },
