        this.projects = resp.data.items
      }, error => {
        msgUtil.error('加载项目失败', msgUtil.findErrorMsg(error))
      })
    },
    drawIcons() {
      const city = this.queryFilter.city
      const name = this.queryFilter.name
      const icon = new BMap.Icon('./static/img/project_2.png', new BMap.Size(30, 50))
      const filterProjects = this.projects.filter(i => {
        return (!city || i.city.indexOf(city) >= 0) && (!name || i.name.indexOf(name) >= 0)
      })
      var mark
      var markers = []
      filterProjects.forEach(p => {
        mark = new BMap.Marker(new BMap.Point(p.lng, p.lat), { icon: icon })
        // this.map.addOverlay(mark)
        markers.push(mark)
      })
      // var markerClusterer = new BMapLib.MarkerClusterer(map, { markers: markers })
    }
